import React, {ReactNode} from 'react';
import {FormGroup, Input, Label} from 'reactstrap';

type CheckboxListProps = {
    options: string[];
    label: ReactNode | ((option: string) => string);
    selectedItems: string[];
    setSelectedItems: (selectedItems: string[]) => void;
};

const CheckboxList = ({
    options,
    label,
    selectedItems,
    setSelectedItems
}: CheckboxListProps) => {
    const selectedItemsChanged = (
        event: React.ChangeEvent<HTMLInputElement>,
        option: string
    ) => {
        if (event.currentTarget.checked) {
            setSelectedItems([option, ...selectedItems]);
        } else {
            setSelectedItems(selectedItems.filter(o => o !== option));
        }
    };

    return (
        <>
            {options.map((option, index) => (
                <FormGroup check key={`Check_${index}`}>
                    <Input
                        type="checkbox"
                        id={`checkbox-${index}`}
                        checked={selectedItems.includes(option)}
                        onChange={event => selectedItemsChanged(event, option)}
                    />
                    <Label check for={`checkbox-${index}`}>
                        {typeof label === 'function'
                            ? label(option)
                            : String(option)}
                    </Label>
                </FormGroup>
            ))}
        </>
    );
};

export default CheckboxList;
